:all .3s ease-out}nav>ul{list-style-type:none;margin:0px}.grid>ul{display:grid}.div-dm{display:inline-block}.center{display:block;margin-left:auto;margin-right:auto}.logo-dm{width:50%}.grid>ul{grid-template-columns:repeat(3, 100px);grid-gap:10px;gap:10px;justify-content:center;justify-items:center}@media (min-width: 600px){.grid>ul{grid-template-columns:repeat(4, fit-content(200px))}}.grid>ul>li>a{font-size:large}nav{padding:0px}@media (min-width: 600px){nav{padding:20px}}*,*::before,*::after{box-sizing:inherit}.logo-LI{size:10px}.App{text-align:center;background-color:#f7f7f7}.Portfolio{width:100%;height:100%;overflow:hidden;position:relative}.App-logo{height:40vmin;pointer-events:none}.App-header{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.intro{display:grid;justify-content:center;align-items:center}.App-link{color:#61dafb}input,.champs select{width:50%;font-size:35px;padding:1em 10%;clear:both;text-align:right}input:focus{border:blue 10px}label{background:black;padding:3px 6px;font-size:40px;margin-left:1px;font-weight:normal;clear:both}

